import React, { Component } from 'react'
import CoinNewsSection from './coinNewsSection'
import newsService from './services/newsService'

class CoinNewsSectionContainer extends Component {
  state = { news: [], loading: true }

  async componentDidMount() {
    const news = await newsService.getNews('BTC', 3, 0)
    this.setState({ news, loading: false })
  }

  loadMore = async () => {
    this.setState((state) => ({ ...state, loading: true }))

    const news = await newsService.getNews('BTC', 6, this.state.news.length)

    this.setState((state) => ({
      ...state,
      news: [...state.news, ...news],
      loading: false,
    }))
  }

  render() {
    return (
      <CoinNewsSection
        news={this.state.news}
        loading={this.state.loading}
        loadMore={this.loadMore}
      />
    )
  }
}

export default CoinNewsSectionContainer
