import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './progress-bar.scss'

class ProgreeBar extends Component {
  render() {
    const valueRatio =
      Math.round((this.props.value / this.props.maxValue) * 10000) / 100

    return (
      <div className="progress-bar">
        <div className="progress-bar-indicator-wrapper">
          <div
            className="progress-bar-indicator"
            style={{
              width: valueRatio + '%',
              backgroundColor: this.props.color,
            }}
          />
        </div>
        <div className="progress-bar-value">{Math.round(valueRatio) + '%'}</div>
      </div>
    )
  }
}

ProgreeBar.propTypes = {
  value: PropTypes.number,
  maxValue: PropTypes.number,
  color: PropTypes.string,
}

ProgreeBar.defaultValues = {
  value: 1,
  maxValue: 2,
}

export default ProgreeBar
