import React, { Component } from 'react'
import CoinRatingsPage from './coinRatingsPage'
import coinRatingsService from './services/coinRatingsService'

class CoinRatingsPageContainer extends Component {
  state = { coinRatingData: null, loading: true }

  async componentDidMount() {
    const coinRatingData = await coinRatingsService.getCoinRatingData('BTC')

    this.setState(() => ({ coinRatingData, loading: false }))
  }

  render() {
    return (
      <CoinRatingsPage
        coinRatingData={this.state.coinRatingData}
        loading={this.state.loading}
      />
    )
  }
}

export default CoinRatingsPageContainer
